.Icon-container{
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  height:48px;
  width:48px;
  font-size:24px;
  font-weight:700
}
.Icon-container-check{
  position:absolute;
  left:12px
}
.Icon-container-check--active{
  color:#4caf50
}
.Icon-container-delete{
  position:absolute;
  top:-24px;
  right:0
}
.Icon-container-delete:hover .Icon-svg{
  fill:red;
}
.Icon-container-check:hover .Icon-svg{
  fill:green;
}
.Icon-svg {
  width: 1.5rem;
  height: 1.5rem;
}